.footer {
  height: 200px;
  background-image: url(../images/photos/footer-bg-mb.png);
  background-position: center;
  background-size: cover;

  @include onTablet {
    height: 480px;
    background-image: url(../images/photos/footer-bg.png);
  }

  @include onDesktop {
    height: 555px;
    background-image: url(../images/photos/footer-bg.png);
  }
}
